import React, { useState } from "react";
import { Dropdown } from 'primereact/dropdown';

export default function Contact({ disableImage }) {

    const [selectedCity, setSelectedCity] = useState(null);
    const cities = [
        { name: 'New York', code: 'NY' },
        { name: 'Rome', code: 'RM' },
        { name: 'London', code: 'LDN' },
        { name: 'Istanbul', code: 'IST' },
        { name: 'Paris', code: 'PRS' }
    ];

    return (
        <section className='py-3 lg:py-10 bg-white'>

            <div className='container grid grid-cols-12 gap-5 lg:gap-8'>

                <form className='col-span-12 lg:col-span-6 py-5' onSubmit={e => e.preventDefault}>


                    <h1 className='mb-8 font-bold text-[#3F2621] text-[40px]'>Contacto</h1>

                    <div className='flex items-center justify-between'>

                        <div className='w-[49%]'>

                            <label className='text-[#3F2621] font-medium'>Nombre</label>

                            <input type='text' className='p-3 border-b border-b-[#D9D4D3] w-full text-[13px] placeholder:text-[#3F2621] placeholder:opacity-40' placeholder='Su nombre' />

                        </div>

                        <div className='w-[49%]'>

                            <label className='text-[#3F2621] font-medium'>Apellido</label>

                            <input type='text' className='p-3 border-b border-b-[#D9D4D3] w-full text-[13px] placeholder:text-[#3F2621] placeholder:opacity-40' placeholder='Apellido' />

                        </div>

                    </div>


                    <div className='my-10'>

                        <label className='text-[#3F2621] font-medium'>Empresa</label>

                        <input type='text' className='p-3 border-b border-b-[#D9D4D3] w-full text-[13px] placeholder:text-[#3F2621] placeholder:opacity-40' placeholder='Su nombre' />

                    </div>

                    <div className='my-10 form-dropdown'>

                        <label className='text-[#3F2621] font-medium'>Motivo del envío</label>

                        <Dropdown value={selectedCity} onChange={(e) => setSelectedCity(e.value)} options={cities} optionLabel="name"
                            placeholder="Iniciar sesión como vendedor" className="w-full md:w-14rem" />

                    </div>

                    <div className='my-10'>

                        <label className='text-[#3F2621] font-medium'>Tu mensaje</label>

                        <textarea rows={4} className="block resize-none mt-3 w-full border border-[#D9D4D3] !font-serif p-3 rounded-[5px] !font-normal text-[13px] placeholder:text-[#3F2621] placeholder:opacity-40" placeholder="Tipo.."></textarea>


                    </div>

                    <button className="p-6 w-full text-white text-center font-[500] text-[24px] rounded-[50px] bg-[#45B8EA]">enviar</button>


                </form>

                <div className={`col-span-12 lg:col-span-6 ${disableImage ? "hidden lg:flex" : "flex"} items-center justify-end p-5 -order-1 lg:order-[initial]`}>

                    <img src={require('./contacta.png')} className="w-full lg:w-[90%] h-full object-fill" />

                </div>

            </div>

        </section>
    )
}
